@import url('https://fonts.googleapis.com/css2?family=Roboto&display=swap');

* {
  margin: 0;
  padding: 0;
  font-family: 'Roboto', sans-serif;
  box-sizing: border-box;
  -webkit-tap-highlight-color: transparent;
}

.navbar {
  position: sticky;
  top: 0;
  background-color: #fafbfc;
  height: 70px;
  display: flex;
  align-items: center;
  z-index: 10;
  box-shadow: inset 2px 2px 2px 0px rgba(60, 60, 60, 0.5),
    5px 5px 16px 0px rgba(255, 255, 255, 0.1),
    4px 4px 5px 0px rgba(49, 48, 48, 0.1);
}

.navbar img {
  height: 55px;
  padding-left: 15px;
}

.navbar .links {
  margin: auto;
}

.navbar .links ul {
  display: flex;
}

.navbar .links ul li {
  list-style: none;
  padding: 10px;
}

.navbar .links ul li:hover {
  cursor: pointer;
}

.home {
  /* background-color: red; */
  height: calc(100vh - 70px);
  display: flex;
  justify-content: center;
  /* align-items: center; */
  flex-wrap: wrap;
  padding-top: 20px;
}

.home .imgs {
  /* background-color: red; */
  /* overflow: hidden; */
  display: flex;
  height: 250px;
  width: 500px;
}

.home .imgs img {
  width: 95vw;
  margin: auto;
  border-radius: 32px;
  max-width: 500px;
  height: 250px;
  box-shadow: 2px 2px 10px 1px rgb(35, 123, 255);
  /* transform: translateX(100px); */
  transition: 1s;
  /* animation: imageAnimate 1s ease 2s infinite; */
}

.home .container {
  padding: 0 20px;
  /* background-color: blue; */
}

.home .container .tagline {
  font-size: 45px;
  /* background-color: rebeccapurple; */
  display: block;
  text-align: center;

}

.home .container p {
  font-size: 18px;
  padding-top: 20px;
}

.events {
  height: calc(100vh - 70px);
  background: linear-gradient(10deg, #009ffd, #2a2a72);
  padding: 10px;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
}

.events .upcomingEvents {
  color: white;
}

.events .upcomingEvents p {
  font-weight: bold;
  font-size: 25px;
}

.events .upcomingEvents .eventList {}

.eventItem {
  background-color: rgba(153, 1, 255, 0.413);
  max-width: 150px;
  margin: 10px;
  border-radius: 12px;
  height: 35vh;
  display: flex;
  flex-wrap: wrap;
  position: relative;
}

.eventItem .eventInfo {
  /* background-color: red; */
  position: absolute;
  width: 150px;
  height: 100%;
  right: 0;
  top: 0;
}

.eventItem .img {
  display: flex;
  justify-content: center;
  padding: 10px;
}

.eventItem .img img {
  height: 90px;
  border-radius: 12px;
}

.eventItem .eventTitle {
  font-size: 30px;
  text-align: center;
}

.eventItem .eventDescription p {
  font-weight: 400;
  font-size: 15px;
  padding: 5px;
}

.eventItem .btn {
  /* background-color: red; */
  display: flex;
  justify-content: center;
}

.eventItem .btn button {
  height: 25px;
  width: 100px;
  border: 0;
  border-radius: 12px;
  cursor: pointer;
  position: absolute;
  bottom: 5px;
}

.about {
  height: calc(100vh - 70px);
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-top: 25px;
}

.about .aboutInfo {
  padding-top: 25px;
}

@media (min-width: 200px) and (max-width: 600px) {
  .home .container {
    padding-top: 20px;
  }

  .home .imgs {
    width: 300px;
    height: 210px;
  }

  .home .imgs img {
    max-width: 300px;
    height: 210px;
  }

  .home .container .tagline {
    font-size: 28px !important;
  }

  .events {
    justify-content: flex-start;
  }


  .eventItem {
    position: relative;
  }

  .eventItem .eventInfo {
    position: absolute;
    top: 120px;
    height: calc(35vh - 120px);
  }
}

@media (max-width: 600px) {
  .home {
    position: relative;
  }

  .home .imgs {
    height: 30%;
    position: absolute;
    top: 5%;
  }

  .home .container {
    position: absolute;
    top: 40%;
  }

  .home .container .tagline {
    font-size: 35px;
  }

  .home .container p {
    font-size: 15px;
  }

  .eventItem .eventInfo {
    width: 100%;
  }

  .eventItem .eventDescription {
    height: 90px;
    /* background-color: blue; */
  }

}

@media (min-width: 600px) {
  .eventItem {
    max-width: 320px;
    height: auto;
    align-items: center;
  }

  .eventItem .eventInfo {
    max-width: 150px;
    /* background-color: red; */
  }
}

@keyframes imageAnimate {
  0% {
    /* opacity: 0; */
    transform: translateX(0);
  }

  100% {
    /* opacity: 1; */
    transform: translateX(-500px);
  }
}